/* App.css */

body {
  margin: 0;
  padding: 0;
  background-color: #0f1115;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #ffffff;
}

header {
  background-color: #1c1f26;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 2px solid #2c2f36;
}

header h1 {
  margin: 0;
  font-size: 2rem;
  color: #61dafb;
}

nav a {
  color: #ffffff;
  margin-left: 1.5rem;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s;
}

nav a:hover {
  color: #61dafb;
}

input[type="text"] {
  padding: 0.5rem 1rem;
  margin-right: 1rem;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 1rem;
  width: 300px;
}

button {
  padding: 0.5rem 1.2rem;
  background-color: #61dafb;
  color: #0f1115;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
}

button:hover {
  background-color: #21a1f1;
}

.movie-card {
  background-color: #1c1f26;
  padding: 1rem;
  margin: 1rem;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  max-width: 600px;
}

.movie-card h2 {
  color: #61dafb;
}

.movie-card p {
  color: #ccc;
}

.movies-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
}
